import { Component, OnInit ,Input} from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm, VhAuth, VhQueryCafe } from 'ionic-vhframeworks';
import { NzModalService } from 'ng-zorro-antd/modal';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';
import { LanguageService } from 'src/app/cafe/services/language.service';
import { SalesPaymentCardA4Component } from 'src/app/cafe/components/print/sales-payment-card/sales-payment-card-a4/sales-payment-card-a4.component';
import { SalesPaymentCardK57Col3Component } from 'src/app/cafe/components/print/sales-payment-card/sales-payment-card-k57-col3/sales-payment-card-k57-col3.component';
import { SalesPaymentCardK80Col3Component } from 'src/app/cafe/components/print/sales-payment-card/sales-payment-card-k80-col3/sales-payment-card-k80-col3.component';
import { SalesPaymentCardK80Col4Component } from 'src/app/cafe/components/print/sales-payment-card/sales-payment-card-k80-col4/sales-payment-card-k80-col4.component';
import { SalesPaymentCardA5Component } from 'src/app/cafe/components/print/sales-payment-card/sales-payment-card-a5/sales-payment-card-a5.component';
import { VhBillType50DetailEdit, VhBillType50Edit } from '../../bill-type/bill-type-50/bill-type50-interface';

@Component({
  selector: 'app-sales-payment-card',
  templateUrl: './sales-payment-card.component.html',
  styleUrls: ['./sales-payment-card.component.scss']
})
export class SalesPaymentCardComponent implements OnInit {
  @Input() id_bill: any;
  public invoiceInfo: any;
  public 
  invoice_detail: VhBillType50DetailEdit[]
  public invoice: VhBillType50Edit
  subTotal = 0
  public itemInvoice: any;
  public radioValue: number;
  public customer: any;
  public tableHeight: any;
  display_promotion_selling_price: boolean = this.vhQueryCafe.getLocalAppSettingNameBranch('permission_branch').display_promotion_selling_price

  constructor(
    private router: Router,
    public vhQueryCafe: VhQueryCafe,
    public vhAuth: VhAuth,
    public vhAlgorithm: VhAlgorithm,
    private languageService: LanguageService,
    private vhComponent: VhComponent,
    private modal: NzModalService,
  ) {
  
     
  }
  ngOnInit(): void {
    this.getDataInvoice(this.id_bill)
  }

  init(invoice: VhBillType50Edit = null, invoiceDetail: VhBillType50DetailEdit[] = null) {
    this.invoice = invoice
    this.invoice_detail = invoiceDetail
    if (invoice && invoiceDetail) {
      this.invoiceInfo = {
        employee: this.vhAuth.getlocalEmployee(this.invoice.id_employee),
        partner: this.vhQueryCafe.getlocalCustomer(this.invoice.id_customer),
        paymentName: this.setPaymentName(this.invoice.payment_type)
      }
    } else this.invoiceInfo = { employee: {}, partner: {}, paymentName: '' }
    this.setSubTotal()
  }
  setSubTotal() {
    this.subTotal = this.invoice_detail.reduce((prev: number, next) => prev + 1 * next.price, 0)

  }

  getDataInvoice(id) {
    this.vhComponent.showLoading("", "transparent-loading").then(() => {
      Promise.all([
        this.vhQueryCafe.getBill(id),
        this.vhQueryCafe.getBill_details_byId_bill(id)
      ]).then(([invoice, invoiceDetail]: any) => {
        invoiceDetail.forEach(async (element, index) => {
          if (element.id_payment_card_code) {
            let paymentcard = await this.vhQueryCafe.getPaymentCard_Code_Name_Release(element.id_payment_card_code)
            element.name = paymentcard['name_payment_card']
            element.code = paymentcard['code']
            element.products = paymentcard['products']
            element.product_name = this.renderproductname(paymentcard['products']) 
          }
          if (index == invoiceDetail.length - 1) {
            invoiceDetail = this.vhAlgorithm.sortVietnamesebyASC(invoiceDetail, 'name');
            this.init(invoice, invoiceDetail)
            this.vhComponent.hideLoading(0);
          }
        });


      })
    })

  }


  renderproductname(data) {
    let name = ''
    for (let element of data) {
      let product = this.vhQueryCafe.getlocalDetailProduct(element.id_subproduct ? element.id_subproduct : element.id_product)
      element.name =product.name;
      element.unit =product.unit;
      name += '- '+    element.quantity +'x ' + product.name + ' (' + this.vhQueryCafe.getUnit_byRatio(product.units,element.ratio ).unit  +') \n';
    } 
    return name
  }


  goBack() {
     this.vhComponent.hideModal()
  }

  visibleInfoCustomer: boolean = false;
  openInfoCustomer() {
    if (this.itemInvoice.id_customer != 'id_retail') {
      this.customer = this.vhQueryCafe.getlocalCustomer(this.itemInvoice.id_customer);
      this.visibleInfoCustomer = true;
    }
  }
  closeInfoCustomer(e) {
    this.visibleInfoCustomer = false;
  }

  public setPaymentName(type: 1 | 2 | 3) {
    switch (type) {
      case 1: return this.languageService.translate("Cash")
      case 2: return this.languageService.translate("Debit")
      case 3: return this.vhQueryCafe.getlocalWallet(this.invoice.id_wallet).name
    }
  }
  getNameProduct(id_prod: string, id_subProd: string) {
    let product = this.vhQueryCafe.getlocalProduct(id_prod)
    let subFood: any
    if (id_subProd) subFood = product.subs.find(item => item._id == id_subProd)
    return { name: `${product ? product.name : ''} ${subFood ? '(' + subFood.name + ')' : ''}` }
  }


  /**
   * tr v chui tn danh sch sn phm trong voucher
   * @param products 
   * @returns string
   * @example let name = this.getStringName([])
   */
  getStringName(products: any[]) {
    let name = ''
    products.forEach(element => {
      let product = this.vhQueryCafe.getlocalDetailProduct(element.id_subproduct ? element.id_subproduct : element.id_product)
      element.name =product.name;
      element.unit =product.unit;
      name += '- ' + element.quantity + 'x ' + product.name + ' (' + this.vhQueryCafe.getUnit_byRatio(product.units, element.ratio).unit + ') \n';
    });
    return name
  }

  deleteInvoice() {
    if (this.vhAuth.checkMyPermission("sell_enable_cancel_bill")) {
      this.vhComponent.alertConfirm(this.languageService.translate("Confirm"), "", `${this.languageService.translate("Are you sure to cancel the sales invoice")}?`, "OK", this.languageService.translate("Cancel"))
        .then(() => {
          this.vhComponent.showLoading("", "transparent-loading").then(() => {
            this.vhQueryCafe.deleteBill_Billdetail(this.invoice._id).then((bool) => {
              if (bool) {
                this.vhComponent.hideLoading(0).then(() => {
                  this.goBack()
                  this.vhComponent.alertMessageDesktop("success",
                    `${this.languageService.translate("Sales invoice")} ${this.invoice.bill_code} ${this.languageService.translate("has been cancelled successfully")}`, "success-toast")
                })
              } else {
                this.vhComponent.hideLoading(0).then(() => {
                  this.vhComponent.alertMessageDesktop("error",
                    `${this.languageService.translate("V c payment_card  c i ly hng ha nn ha n bn payment_card ny khng th xa")}`)
                })
              }

            })
          })
        }, () => { })
    } else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }
  public renderAddress(partner) {
    return (partner.address ? partner.address : '') +
      (partner.district ? ', ' + partner.district : '') +
      (partner.province ? ', ' + partner.province : '') +
      (partner.country ? ', ' + partner.country : '')
  }

  printInvoice() {
    let print = this.vhQueryCafe.getLocalPrintPage('page_desktop_sales_payment_card')
    if (print && print.print_sizes[print.print_sizes.findIndex(e => e._id == print.print_size_default)].enable) {
      let printer =  print.print_sizes[print.print_sizes.findIndex(e => e._id == print.print_size_default)] 
      if (printer) {
        let type = printer._id;
        this.vhComponent.showModal(this.renderPrintPage(type), { printer: printer ,invoice_detail : this.invoice_detail, invoice: this.invoice }, false, false, `modal-print-${type}`).then((modal) => {
    
        })
      }
    }else{
      this.modal.confirm({
        nzTitle: '<i>' + this.languageService.translate("Function") + '</i>',
        nzContent: '<b>' + this.languageService.translate("Please open the function in") + ' ' + '<i>' + '"' + this.languageService.translate("Sales page settings") + '"' + '</i>' + ' ' + ' -> ' + '<i>' + '"' + this.languageService.translate('Print page') + '"' + '</i>' + '</b>',
        nzIconType: "info-circle",
        nzCancelText: null,
        nzOnOk: () => console.log('OK'),
      });
    }

  }


  /**
   * tr v component ca trang in tng ng
   * @example this.renderPrintPage('print_size_a4')
   * @returns component
   */
  private renderPrintPage(type: 'print_size_a4' | 'print_size_a5' | 'print_size_k80_4c' | 'print_size_k80_3c' | 'print_size_k57_3c') {
    switch (type) {
      case 'print_size_k57_3c': return SalesPaymentCardK57Col3Component
      case 'print_size_k80_3c': return SalesPaymentCardK80Col3Component
      case 'print_size_k80_4c': return SalesPaymentCardK80Col4Component
      case 'print_size_a5': return SalesPaymentCardA5Component
      case 'print_size_a4': return SalesPaymentCardA4Component
      default: return SalesPaymentCardA4Component
    }
  }

}
